<template>
  <div>
    <div class="mb-5">
      <!-- 筛选 -->
      <v-item-group v-model="select" class="mb-2">
        <v-item
          v-for="(item, index) in category"
          :key="index"
          v-if="!(item.type2[0] === 6)"
          :value="item.type2"
          v-slot="{ active, toggle }"
          class="mr-2 mb-2">
          <v-btn
            outlined
            small
            :class="active ? 'grey darken-4 white--text' : ''"
            class="px-1"
            @click="toggle">
            {{ item.name }}
          </v-btn>
        </v-item>
      </v-item-group>

      <!-- 搜索 -->
      <div class="d-flex justify-space-between">
        <v-text-field
          v-model="keyword"
          solo
          prepend-inner-icon="mdi-magnify"
          hide-details="false"
          placeholder="搜索.."
          clearable
          color="initial"
          style="font-size: 0.8em"></v-text-field>
      </div>
    </div>

    <v-card class="pa-5">
      <v-data-table
        dense
        :header-props="{ sortIcon: 'mdi-menu-up' }"
        :headers="headers"
        :items="pagedList"
        :items-per-page="page"
        :custom-sort="customSort"
        hide-default-footer
        mobile-breakpoint>
        <!-- 名称+图标 -->
        <template v-slot:[`item.Name`]="{ item }">
          <div class="d-flex align-center w-[500px] caption text-no-wrap">
            <!-- 图标 -->
            <tooltip :item="item" :category="category" :show-template-flag="showTemplateFlag" />
            <!-- 名称 -->
            {{ item.Name }}

            <!-- 模板 -->
            <div
              v-if="showTemplateFlag(item)"
              class="ml-1 d-flex align-center justify-center rounded-sm blue-grey darken-4"
              style="width: 12px; height: 12px">
              <span class="cyan--text text--accent-1" style="font-size: 0.7em">T</span>
            </div>
          </div>
        </template>

        <!-- 分类 -->
        <template v-slot:[`item.Type`]="{ item }">
          <span class="caption">
            <category-chip :item="item" small />
          </span>
        </template>

        <!-- 伤害 -->
        <template v-slot:[`item.Att`]="{ item }">
          <span class="caption">
            {{ item.Att }}
          </span>
        </template>

        <!-- 子弹/弹夹数 -->
        <template v-slot:[`item.Bullet`]="{ item }">
          <span class="d-flex caption">
            <span>{{ item.Bullet.toString().padStart(3, '0') }}</span>
            <span>/</span>
            <span>
              {{
                isNaN(item.MaxBullet / item.Bullet)
                  ? '0'.toString().padStart(2, '0')
                  : (item.MaxBullet / item.Bullet).toString().padStart(2, '0')
              }}
            </span>
          </span>
        </template>

        <!-- 弹速 -->
        <template v-slot:[`item.Bullet_Speed`]="{ item }">
          <span class="caption">
            {{ item.Bullet_Speed }}
          </span>
        </template>

        <!-- 延迟 -->
        <template v-slot:[`item.Delay`]="{ item }">
          <span class="caption">
            {{ item.Delay }}
          </span>
        </template>

        <!-- 射程 -->
        <template v-slot:[`item.Distance`]="{ item }">
          <span class="caption">
            {{ item.Distance }}
          </span>
        </template>

        <!-- 后坐力 -->
        <template v-slot:[`item.Accuracy`]="{ item }">
          <span class="caption">
            {{ item.Accuracy }}
          </span>
        </template>
      </v-data-table>

      <!-- 分页 -->
      <div v-if="pagedList.length" class="text-center py-2">
        <v-pagination
          color="grey darken-4"
          v-model="currentPage"
          :length="total"
          :total-visible="5"
          @input="changePage"></v-pagination>
      </div>
    </v-card>
  </div>
</template>

<script>
import { mapActions, mapGetters, mapState } from 'vuex';
import _ from 'lodash';
import Tooltip from '@/views/weapon/components/tooltip.vue';
import CategoryChip from '@/components/categoryChip.vue';

export default {
  components: { Tooltip, CategoryChip },
  data() {
    return {
      headers: [
        {
          text: '名称',
          value: 'Name',
          align: 'start',
          class: 'caption',
          width: '250px',
        },

        {
          text: '分类',
          value: 'Type',
          align: 'start',
          class: 'caption',
          width: '80px',
          sortable: false,
        },

        {
          text: '伤害',
          value: 'Att',
          align: 'start',
          class: 'caption',
          width: '80px',
        },

        {
          text: '子弹/弹夹数',
          value: 'Bullet',
          align: 'start',
          class: 'caption',
          width: '120px',
        },

        {
          text: '弹速',
          value: 'Bullet_Speed',
          align: 'start',
          class: 'caption',
          width: '80px',
        },

        {
          text: '延迟',
          value: 'Delay',
          align: 'start',
          class: 'caption',
          width: '80px',
        },

        {
          text: '射程',
          value: 'Distance',
          align: 'start',
          class: 'caption',
          width: '80px',
        },

        {
          text: '后坐力',
          value: 'Accuracy',
          align: 'start',
          class: 'caption',
          width: '80px',
        },
      ],

      category: [
        {
          name: '步枪',
          type: 8,
          type2: [2, 8],
        },
        {
          name: '狙击枪',
          type: 8,
          type2: [3],
        },
        {
          name: '反坦克炮',
          type: 8,
          type2: [4],
        },
        {
          name: '散弹枪',
          type: 8,
          type2: [7],
        },
        {
          name: '手枪',
          type: 8,
          type2: [1],
        },
        {
          name: '未知',
          type: 8,
          type2: [6],
        },
      ],

      keyword: '',

      select: null,

      currentPage: 1,

      page: 50,
    };
  },

  computed: {
    ...mapState('weapon', ['weaponList']),

    total() {
      return Math.ceil(this.filterList.length / this.page);
    },

    filterList() {
      let list = [];

      // 筛选
      if (!this.select) {
        list = this.weaponList;
      } else {
        list = this.weaponList.filter(item => this.select.includes(item.Type2));
      }

      // 搜索
      if (this.keyword?.trim()) {
        return list.filter(item => _.includes(item.Name.toString().toLowerCase(), this.keyword.toLowerCase()));
      }

      return list;
    },

    pagedList() {
      const start = (this.currentPage - 1) * this.page;

      const end = start + this.page;

      return this.filterList.slice(start, end);
    },
  },

  mounted() {},

  methods: {
    ...mapActions('weapon', ['fetchData']),

    customSort(items, sortBy, sortDesc) {
      if (!sortBy.length) {
        // 默认排序
        return items;
      }

      const [isSortDesc] = sortDesc;
      const [sort] = sortBy; // 排序列字段

      const start = (this.currentPage - 1) * this.page;
      const end = start + this.page;

      if (isSortDesc) {
        // 降序+分页
        return _.sortBy(this.filterList, item => item[sort])
          .reverse()
          .slice(start, end);
      } else {
        // 升序+分页
        return _.sortBy(this.filterList, item => item[sort]).slice(start, end);
      }
    },

    changePage(newPage) {
      this.currentPage = newPage;
    },

    // 是否为模板属性
    showTemplateFlag(item) {
      const weaponConfigs = {
        // 手枪
        rifle: {
          Att: 43,
          Bullet: 35,
          MaxBullet: 140,
          Bullet_Speed: 2150,
          Delay: 95,
          Distance: 1450,
          Accuracy: 28,
        },
        // 狙击枪
        sniper: {
          Att: 142,
          Bullet: 5,
          MaxBullet: 20,
          Bullet_Speed: 12000,
          Delay: 4000,
          Distance: 3020,
          Accuracy: 15,
        },
        // 反坦克炮
        antiTank: {
          Att: 1210,
          Bullet: 3,
          MaxBullet: 3,
          Bullet_Speed: 1690,
          Delay: 4500,
          Distance: 1800,
          Accuracy: 10,
        },
        // 散弹枪
        shotgun: {
          Att: 130,
          Bullet: 9,
          MaxBullet: 27,
          Bullet_Speed: 2000,
          Delay: 480,
          Distance: 700,
          Accuracy: 30,
        },
        // 手枪
        pistol: {
          Att: 40,
          Bullet: 12,
          MaxBullet: 36,
          Bullet_Speed: 1850,
          Delay: 260,
          Distance: 1050,
          Accuracy: 40,
        },
      };

      for (const weapon in weaponConfigs) {
        const config = weaponConfigs[weapon];

        let isMatch = true;

        for (const prop in config) {
          if (item[prop] !== config[prop]) {
            isMatch = false;
            break;
          }
        }

        if (isMatch) {
          return true;
        }
      }

      return false;
    },
  },

  watch: {
    select() {
      this.currentPage = 1;
    },
    keyword() {
      this.currentPage = 1;
    },
  },
};
</script>
