<template>
  <div class="d-flex align-center w-[500px]">
    <v-tooltip top transition="fade-transition" min-width="256" max-width="256">
      <!-- 图标 -->
      <template v-slot:activator="{ on, attrs }">
        <div
          style="
            margin-right: 10px;
            width: 22px;
            height: 22px;
            border-radius: 4px;
            overflow: hidden;
          "
          v-bind="attrs"
          v-on="on">
          <img
            style="width: 28px; height: 28px; margin-top: -3px; margin-left: -3px"
            :src="`/image/icon/${item.Icon?.toString().padStart(5, '0')}.jpg`" />
        </div>
      </template>

      <div class="pa-3">
        <!-- 标题 -->
        <div class="d-flex align-center justify-space-between">
          <!-- 图标+名称 -->
          <div class="d-flex align-center">
            <!-- 图标 -->
            <img
              :src="`/image/icon/${item.Icon?.toString().padStart(5, '0')}.jpg`"
              alt=""
              class="mr-2 rounded" />
            <!-- 名称 -->
            <span class="font-weight-medium subtitle-2 white--text">
              {{ item.Name }}
            </span>
          </div>

          <!-- 模板 -->
          <!-- <div
            v-if="showTemplateFlag(item)"
            class="ml-2 d-flex align-center justify-center rounded-sm blue-grey darken-4"
            style="width: 14px; height: 14px">
            <span class="cyan--text text--accent-1" style="font-size: 0.8em">T</span>
          </div> -->
        </div>

        <!-- 外观 -->
        <div class="my-6 d-flex align-center justify-center">
          <img
            :src="`/image/aspect/${item.Aspect?.toString().padStart(4, '0')}.png`"
            alt=""
            class="mr-2 rounded" />
        </div>

        <!-- 分割线 -->
        <v-divider v-if="!(item.Type === 11)" class="my-2 grey darken-3"></v-divider>

        <!-- 属性 -->
        <div class="text--secondary caption">
          <div class="white--text">
            分类：
            <span class="green--text text--lighten-1">
              <span v-for="(c, index) in category" :key="index">
                <span v-if="c.type2.includes(item.Type2)">{{ c.name }}</span>
              </span>
            </span>
          </div>
        </div>

        <!-- 分割线 -->
        <v-divider v-if="!(item.Type === 11)" class="my-2 grey darken-3"></v-divider>

        <!-- 属性 -->
        <div class="text--secondary caption">
          <!-- 伤害 -->
          <div v-if="item.Att" class="white--text">
            伤害：
            <span class="green--text text--lighten-1">{{ item.Att }}</span>
          </div>

          <!-- 子弹/弹夹数 -->
          <div v-if="item.Bullet" class="white--text">
            子弹/弹夹数：
            <span class="green--text text--lighten-1">
              <span>{{ item.Bullet.toString().padStart(3, '0') }}</span>
              <span>/</span>
              <span>{{ (item.MaxBullet / item.Bullet).toString().padStart(2, '0') }}</span>
            </span>
          </div>

          <!-- 弹速 -->
          <div v-if="item.Bullet_Speed" class="white--text">
            弹速：
            <span class="green--text text--lighten-1">{{ item.Bullet_Speed }}</span>
          </div>

          <!-- 延迟 -->
          <div v-if="item.Delay" class="white--text">
            延迟：
            <span class="green--text text--lighten-1">{{ item.Delay }}</span>
          </div>

          <!-- 射程 -->
          <div v-if="item.Distance" class="white--text">
            射程：
            <span class="green--text text--lighten-1">{{ item.Distance }}</span>
          </div>

          <!-- 后坐力 -->
          <div v-if="item.Accuracy" class="white--text">
            后坐力：
            <span class="green--text text--lighten-1">
              {{ item.Accuracy }}
            </span>
          </div>
        </div>

        <!-- 分割线 -->
        <v-divider class="my-2 grey darken-3"></v-divider>

        <!-- 简介 -->
        <div v-if="item.Explain" class="white--text">
          <span class="caption" v-html="content"></span>
        </div>
      </div>
    </v-tooltip>
  </div>
</template>

<script>
export default {
  props: ['item', 'category', 'showTemplateFlag'],
  data() {
    return {};
  },
  methods: {},

  mounted() {},

  computed: {
    // 简介
    content() {
      const content = this.item.Explain;
      return content
        .toString()
        .replace(/\/b\/c\(.+?\)(.+?)\/c\/b/g, '<b style="color: rgba(255,228,000);">$1</b>');
    },
  },
};
</script>
