<template>
  <div>
    <v-chip
      v-if="item.Type === 8 && (item.Type2 === 8 || item.Type2 === 2)"
      :class="categoryColor[0]"
      :x-small="xSmall"
      :small="small"
      label
      text-color="white">
      步枪
    </v-chip>

    <v-chip
      v-else-if="item.Type === 8 && item.Type2 === 3"
      :class="categoryColor[1]"
      :x-small="xSmall"
      :small="small"
      label
      text-color="white">
      狙击枪
    </v-chip>

    <v-chip
      v-else-if="item.Type === 8 && item.Type2 === 4"
      :class="categoryColor[2]"
      :x-small="xSmall"
      :small="small"
      label
      text-color="white">
      反坦克炮
    </v-chip>

    <v-chip
      v-else-if="item.Type === 8 && item.Type2 === 7"
      :class="categoryColor[3]"
      :x-small="xSmall"
      :small="small"
      label
      text-color="white">
      散弹枪
    </v-chip>

    <v-chip
      v-else-if="item.Type === 8 && item.Type2 === 1"
      :class="categoryColor[4]"
      :x-small="xSmall"
      :small="small"
      label
      text-color="white">
      手枪
    </v-chip>

    <v-chip v-else :x-small="xSmall" :small="small" label text-color="white">未知</v-chip>
  </div>
</template>

<script>
export default {
  props: {
    item: {
      required: true,
    },
    xSmall: {
      type: Boolean,
      default: false,
    },
    small: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      categoryColor: [
        'cyan lighten-1',
        'pink lighten-1',
        'purple lighten-1',
        'orange lighten-1',
        'teal lighten-1',
      ],
    };
  },
};
</script>
